@import "../variables";
.project{
  width: 100%;
  box-shadow: 1px 1px 4px black;
  transition: all 200ms ease-out;
  position: relative;
  margin-top: 1rem;
  text-decoration: none!important;
  background: $light;
  &:hover{
    transform: scale(1.1);
    cursor: pointer;
    text-decoration: none!important;
  }
  .image{
    width: 100%;
    position: relative;
    img{
      width: 100%;
    }
  }
  .ct-title{
    width: 100%;
    padding: 0.8rem 0.5rem;
    background: $gradient;
    color: white;
    text-align: center;
//    position: absolute;
//    bottom: 0;
//    left: 0;
  }
  .link{
    &:hover{
      text-decoration: none;
    }
  }
  .description{
    color: #707070;
    background: $light;
    padding: 1rem;
  }
}
